import { VFC } from 'react';
import styled from "styled-components";

type IconLinkProps = {
    href: string,
    src: string,
    alt?: string,
    width?: string,
}

export const IconLink:VFC<IconLinkProps> = ({href,src,alt,width})=> {
    return(
        <a href={href}><StyledIconLink src={src} alt={alt} width={width}></StyledIconLink></a>
    )
}

const StyledIconLink =styled.img`
    width:${(props)=>props.width};
    height:auto;
    margin-right:7px;
    padding: 8px 16px;
&:hover{
    background: #383d43;
	transition: 0.2s ;
}
`